.door {
    border: double 10px #0000ff;
    width: 50px;
    height: 50px;
    padding: 10px 20px;
    text-align: center;
    font-weight: bold;
}
.animated {
    animation: walk 3s;
}
.open-animation {
    animation: open 3s;
}

@keyframes walk {
    0% {
        width: 50px;
        height: 50px;
    }
    33% {
        width: 100px;
        height: 100px;
    }
    66% {
        width: 150px;
        height: 150px;
    }
    100% {
        width: 200px;
        height: 200px;
    }
}
@keyframes open {
    0% {
        background-color: #000;
    }
    33% {
        background-color: #333;
    }
    66% {
        background-color: #999;
    }
    100% {
        background-color: #ddd;
    }
}

/*
